<template>
  <div class="home">
    <!-- 타이틀 -->
    <div class="title"> 마진율 계산기 ver 1</div>
    <!-- 설정 인풋 필드 -->
    <div class="inputs-group">
      <label for="product"> 상품명
        <input v-model="product" type="text" id="product">
      </label>
      <label for="quantity"> 주문 수량
        <input v-model="quantity" type="text" id="quantity">
      </label>
      <label for="list-price"> 공급 가격
        <input v-model="listPrice" type="text" id="list-price">
      </label>
      <!-- 할인 관련 인풋 -->
      <div class="sale-section">
        <label class="sale" for="sale-quantity"> 할인 최소 수량
          <input v-model="saleQuantity" type="text" id="sale-quantity">
        </label>
        <label class="sale" for="sale-price"> 할인 가격
          <input v-model="salePrice" type="text" id="sale-price">
        </label>
      </div>
      <label for="retail-price"> 쇼핑몰 가격
        <input v-model="retailPrice" type="text" id="retail-price">
      </label>
      <label for="package-price"> 1개당 택배 가격
        <input v-model="packagePrice" type="text" id="package-price">
      </label>
      <button @click="setResultValue"> 계산하기 </button>
    </div>
    <!-- 결과 창 -->
    <div class="result" v-html="result"></div>
  </div>
</template>

<style lang="scss" scoped>
.home {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 1rem;
}
.title {
  font-size: 1.5rem;
  font-weight: bold;
}
.inputs-group {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin-top: 1rem;
  label {
    display: flex;
    flex-direction: column;
    margin-right: 1rem;
    padding: 1.25rem 0rem;
  }
  label.sale {
    padding: 1rem 0rem;
  }
  .sale-section {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    border: 0.25rem solid red;
    border-radius: 1rem;
    padding: 0 1rem;
    margin-right: 1rem;
  }
  button {
    width: 10rem;
    height: 3rem;
    font-size: 1.25rem;
    border: none;
    border-radius: 2rem;
    background: #5ab7ff;
    color: white;
    font-weight: bold;
    &:active {
      background: darken(#5ab7ff, 10%);
    }
    &:hover {
      cursor: pointer;
    }
  }
}
</style>

<script>
export default {
  name: 'Home',
  data () {
    return {
      product: '',
      quantity: 0,
      listPrice: 0,
      saleQuantity: 0,
      salePrice: 0,
      retailPrice: 0,
      packagePrice: 0,
      result: ''
    }
  },
  methods: {
    setResultValue () {
      const _isSale = parseInt(this.quantity) >= parseInt(this.saleQuantity)
      let _purchasedPrice = 0
      if (_isSale) {
        _purchasedPrice = this.quantity * this.salePrice
      } else {
        _purchasedPrice = this.quantity * this.listPrice
      }
      const _profit = (this.quantity * this.retailPrice) - _purchasedPrice - (this.packagePrice * this.quantity)
      const _percent = (_profit / (this.quantity * this.retailPrice)) * 100
      this.result = `${this.product}를 ${this.quantity}개 주문할 경우<br>
                    예상 주문가는 ${_purchasedPrice.toLocaleString()}원 이고<br>
                    모두 판매했을 경우<br>
                    예상 마진은 ${_profit.toLocaleString()}원<br>
                    예상 마진율은 ${_percent.toFixed(2)}% 입니다`
    }
  }
}
</script>
