<template lang="pug">
  div(class="Cal2")
    p 도매가격 계산기 버전 2
    div(class="inputs-group")
      label(for="product") 상품명
        input(v-model="product" type="text" id="product")
      p {{ product }}
      label(for="quantity") 주문수량
        input(v-model="quantity" type="text" id="quantity" @input="setResultValue")
      p
      label(for="list-price") 공급가격
        input(v-model="listPrice" type="text" id="list-price" @input="setResultValue")
      p {{ listPrice }}
      input(type="radio" id="m0" value="1" v-model="marginPrice")
      label(for="m0") 0%
      input(type="radio" id="m5" value="1.05" v-model="marginPrice")
      label(for="m5") 5%
      input(type="radio" id="m10" value="1.1" v-model="marginPrice")
      label(for="m10") 10%
      p
      label(for="margin-price") 마진율
        input(v-model="marginPrice" type="text" id="margin-price")
      p
      label(for="delivery-price") 택배비
        select(v-model="deliPrice")
          option(value=3000) 3000
          option(value=4000) 4000
          option(value=5000) 5000
          option(value=13000) 13000
        p
        button(@click="setResultValue") 계산하기
    div(class="result" v-html="result")
    //- p {{ result }}
</template>

<style lang="scss" scoped>

</style>

<script>
export default {
  data () {
    return {
      product: '',
      quantity: '',
      listPrice: '',
      marginPrice: '1.05',
      deliPrice: 3000,
      result: ''
    }
  },
  watch: {
    marginPrice () {
      this.setResultValue()
    },
    deliPrice () {
      this.setResultValue()
    }
  },
  methods: {
    showProduct () {
      console.log(this.result = this.quantity * this.listPrice * this.marginPrice)
    },
    setResultValue () {
      const deli = parseInt(this.deliPrice)
      this.result = parseFloat((this.quantity * this.listPrice * this.marginPrice) + deli).toFixed(1)
    }
  }
}
</script>
